import tkiQrcode from '../../components/qrcode.vue'
export default {
	components: {
		tkiQrcode
	},
	onLoad() {
    
  },
  onShow() {
    // console.log(uni.getSystemInfoSync());
    uni.getSystemInfo({
      success: res =>{
        // console.log(res)
        this.height_home = res.windowHeight
      }
    })
    this.creatQrcode()
    
  },
	data() {
		return {
      height_home: 0,
			val: '隔壁王二', // 要生成的二维码值
			size: 400, // 二维码大小
			unit: 'upx', // 单位
			background: '#555', // 背景色
			foreground: '#fff', // 前景色
			pdground: '#32dbc6', // 角标色
			lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
			onval: false, // val值变化时自动重新生成二维码
			loadMake: true, // 组件加载完成后自动生成二维码
			src: '' // 二维码生成后的图片地址或base64
		}
	},
	methods: {
		creatQrcode() {
			this.$refs.qrcode._makeCode()
		},
		saveQrcode() {
			this.$refs.qrcode._saveCode()
		},
		qrR(res) {
			this.src = res
		},
  },
}
